@import '../../styles/utils/fonts';
@import '../../styles/utils/vars';

.modal {
  background: #fff;
  padding: 20px;
  border-radius: 20px;
  width: 540px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  position: relative;
  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
  }
  &__close {
    position: absolute;
    top: 0;
    right: 0;
    padding: 20px;
    border-radius: 50%;
    cursor: pointer;
    background-color: white;
    & svg {
      stroke: black;
      stroke-width: 1;
    }
  }
  &__title {
    font-family: 'Poppins';
    font-weight: 400;
    font-size: 30px;
    text-align: center;
    margin: 20px 80px 40px;
  }
}

.form {
  &__checkbox_container {
    display: flex;
    align-items: center;
    margin-bottom: 42px;
    justify-content: center;
  }

  &__checkbox_label {
    font-family: 'Circe';
    font-weight: 700;
    font-size: 16px;
    color: get-color('gray2');
    cursor: pointer;
    user-select: none;
    display: flex;
    align-items: center;
    margin-right: 24px;
    margin-left: 20px;
    &_expense {
      color: get-color('pink4');
    }
    &_income {
      color: get-color('green1');
    }
  }

  &__checkbox_input {
    display: none;
  }

  &__checkbox_custom {
    width: 80px;
    height: 40px;
    border-radius: 30px;
    border: 1px solid get-color('gray2');
    position: relative;
  }

  &__slider {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    background-color: get-color('pink4');
    color: white;
    position: absolute;
    text-align: center;
    font-size: 30px;
    font-family: 'Poppins';
    font-weight: 400;
    top: 50%;
    box-shadow: 0 6px 15px get-color('pink4');
    transform: translateX(calc(100% - 2px)) translateY(-50%);
    transition: transform 0.3s, background-color 0.3s, box-shadow 0.3s;
  }

  &__checkbox_input:checked + &__checkbox_custom &__slider {
    transform: translateX(calc(-50% + 8px)) translateY(-50%);
    background-color: get-color('green1');
    box-shadow: 0 6px 15px get-color('green1');
  }

  &__input {
    margin-bottom: 40px;
    width: 100%;
    border-bottom: 1px solid get-color('gray2');
    font-family: 'Circe';
    font-size: 18px;
    height: 32px;
    &_flex {
      display: flex;
      gap: 32px;
      margin-bottom: 40px;
      & label {
        width: 181px;
        height: 32px;
        position: relative;
        & input {
          text-align: center;
        }
        & .form__date input {
          text-align: left;
          padding-left: 20px;
          width: 181px;
        }
      }
    }
  }
  &__date {
    opacity: 0.9;
  }
  &__flex_container {
    justify-content: center;
    align-items: center;
    margin: 0 53px;
  }
  &__category {
    width: 100%;
    color: get-color('gray2');
    & option {
      font-family: 'Circe';
      font-size: 18px;
      color: black;
      background-color: rgba(0, 0, 0, 0);
      border-radius: 50px;
      &:hover {
        background-color: white;
        color: get-color('pink4');
      }
    }
    &_active {
      color: black;
    }
  }
  &__btn_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    margin-bottom: 40px;
  }
}
